"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { useCartStore } from "@/lib/store";
import { useAuthStore } from "@/lib/auth-store";
import { ChevronLeft, Lock } from "lucide-react";
import { placeOrder } from "@/lib/api";
import { toast } from "sonner";

export default function CheckoutPage() {
  const [mounted, setMounted] = useState(false);
  const [step, setStep] = useState<"shipping" | "payment-method" | "payment" | "review">("shipping");
  const [isProcessing, setIsProcessing] = useState(false);
  const [orderId, setOrderId] = useState<number | null>(null);
  const [paymentMethod, setPaymentMethod] = useState<"COD" | "ONLINE" | "">("");

  const router = useRouter();
  const items = useCartStore((state) => state.items);
  const getTotalPrice = useCartStore((state) => state.getTotalPrice);
  const clearCart = useCartStore((state) => state.clearCart);
  const { user } = useAuthStore();

  const [shippingData, setShippingData] = useState({
    firstName: user?.name?.split(" ")[0] || "",
    lastName: user?.name?.split(" ")[1] || "",
    email: user?.email || "",
    address: user?.address || "",
    city: user?.city || "",
    state: user?.state || "",
    zipCode: user?.zipCode || "",
  });

  const totalPrice = getTotalPrice();
  const shippingCost = totalPrice > 100 ? 0 : 10;
  const tax = (totalPrice + shippingCost) * 0.08;
  const finalTotal = totalPrice + shippingCost + tax;

  useEffect(() => {
    setMounted(true);
    if (items.length === 0) router.push("/cart");
  }, [items.length, router]);

  if (!mounted) return <div>Loading...</div>;

  const handleShippingSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (Object.values(shippingData).some((v) => !v)) return toast.error("Fill all fields");

    try {
      const payload = {
        customer_name: `${shippingData.firstName} ${shippingData.lastName}`,
        email: shippingData.email,
        address: shippingData.address,
        city: shippingData.city,
        state: shippingData.state,
        pincode: shippingData.zipCode,
        subtotal: totalPrice,
        shipping_cost: shippingCost,
        tax: tax,
        final_total: finalTotal,
        payment_method: "", // set after selection

        items: items.map((item) => ({
          product_id: item.product.id,
          product_name: item.product.name,
          size: item.size,
          color: item.color,
          quantity: item.quantity,
          price: item.product.price,
        })),
      };

      const res = await placeOrder(payload);
      setOrderId(res.order_id);
      toast.success(`Order Created Successfully! Order #${res.order_id}`);

      setStep("payment-method");
    } catch (error) {
      console.error(error);
      toast.error("Order failed, try again");
    }
  };

  const handlePlaceOrder = async () => {
    setIsProcessing(true);

    if (!paymentMethod) return toast.error("Select a payment method");

    if (paymentMethod === "COD") {
      toast.success("Order Placed Successfully!");
      clearCart();
      router.push("/order-confirmation");
    } else {
      toast.success("Redirecting to Razorpay...");
      // integrate razorpay here later
    }

    setIsProcessing(false);
  };

  return (
    <div className="max-w-7xl mx-auto px-4 py-8">
      <Button variant="ghost" onClick={() => router.back()} className="mb-6 gap-2">
        <ChevronLeft className="h-4 w-4" /> Back to Cart
      </Button>

      <h1 className="text-4xl font-bold mb-8">Checkout</h1>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        <div className="lg:col-span-2">
          {/* Shipping */}
          {step === "shipping" && (
            <Card className="p-6 space-y-4">
              <h2 className="text-2xl font-bold mb-4">Shipping Address</h2>
              <form onSubmit={handleShippingSubmit} className="space-y-4">
                {/* fields */}
                <input
                  type="text"
                  placeholder="First Name"
                  className="w-full border p-2"
                  value={shippingData.firstName}
                  onChange={(e) => setShippingData({ ...shippingData, firstName: e.target.value })}
                />
                <input
                  type="text"
                  placeholder="Last Name"
                  className="w-full border p-2"
                  value={shippingData.lastName}
                  onChange={(e) => setShippingData({ ...shippingData, lastName: e.target.value })}
                />
                <input
                  type="email"
                  placeholder="Email"
                  className="w-full border p-2"
                  value={shippingData.email}
                  onChange={(e) => setShippingData({ ...shippingData, email: e.target.value })}
                />
                <input
                  type="text"
                  placeholder="Address"
                  className="w-full border p-2"
                  value={shippingData.address}
                  onChange={(e) => setShippingData({ ...shippingData, address: e.target.value })}
                />

                <Button type="submit" className="w-full bg-accent text-white">
                  Continue to Payment Method
                </Button>
              </form>
            </Card>
          )}

          {/* Payment Method */}
          {step === "payment-method" && (
            <Card className="p-6 space-y-4">
              <h2 className="text-2xl font-bold mb-6">Choose Payment Method</h2>

              <button
                className={`w-full p-4 border rounded-md text-left ${
                  paymentMethod === "COD" && "border-accent bg-accent/10"
                }`}
                onClick={() => {
                  setPaymentMethod("COD");
                  setStep("review");
                }}
              >
                ðŸ’µ Cash on Delivery (COD)
              </button>

              <button
                className={`w-full p-4 border rounded-md text-left ${
                  paymentMethod === "ONLINE" && "border-accent bg-accent/10"
                }`}
                onClick={() => {
                  setPaymentMethod("ONLINE");
                  setStep("payment");
                }}
              >
                ðŸ’³ Online Payment (Razorpay / Card)
              </button>
            </Card>
          )}

          {/* Review */}
          {step === "review" && (
            <Card className="p-6 space-y-4">
              <h2 className="text-2xl font-bold mb-4">Review Order</h2>
              <p><b>Payment Method:</b> {paymentMethod}</p>
              <p><b>Total:</b> â‚¹{finalTotal.toFixed(2)}</p>

              <Button
                className="w-full bg-accent text-white"
                onClick={handlePlaceOrder}
                disabled={isProcessing}
              >
                {isProcessing ? "Placing Order..." : "Place Order"}
              </Button>
            </Card>
          )}
        </div>
      </div>
    </div>
  );
}
